/* TailwindCSS */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Google fonts is configured in index.html */

/* Other styling */
@layer base {
    /* Website selection */
    ::selection {
        @apply bg-blue-300/30;
    }

    /* Default styling for the base website */
    body,
    html {
        @apply m-0 p-0 box-border scroll-smooth bg-slate-400 overflow-x-hidden;
    }

    /* Header styling */
    li {
        @apply ease-out duration-300 p-4 hover:text-blue-300 hover:md:scale-110;
    }

